.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, .5);
  font-family: 'Inter', 'Arial', sans-serif;
  visibility: hidden;
  opacity: 0;
  transition: visibility .3s, opacity .3s linear;
}